*,*::before,*::after{box-sizing:border-box;color:inherit}body{font-family:"Arimo","Helvetica",sans-serif;line-height:1.5;margin:0;padding:0}.container{margin:0 auto;max-width:calc(120px * 6);overflow:hidden;padding:88px 0}@media(max-width: calc(120px * 7.5)){.container{max-width:calc(120px * 4);padding:40px 0}}@media(max-width: calc(120px * 5.5)){.container{max-width:calc(120px * 3)}}@media(max-width: calc(120px * 4)){.container{max-width:calc(120px * 2);padding:18px 0}}@media(max-width: calc(120px * 2.66)){.container{max-width:calc(120px * 1)}}h1,h2,h3,h4,h5,h6{font-family:"Arimo","Helvetica",sans-serif;text-align:center}